.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-header-box {
  color: #5097ec;
  background: #ffffff;

  /* Add a subtle shadow at the bottom */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);

  /* Add margin spacing from next element */
  margin-bottom: 2em;
}

.main-header-box h1 {
  /* Override default margins */
  margin: 0;

  /* Paddings from header-box */
  padding: 0.5em;
}

.product-grid {
  /* By default, grid elements stack vertically. */
  /* We want a grid layout instead. */
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));

  /* Add gap between grid elements */
  gap: 2em;

  margin: 0 100px;
}

.products-title {
  font-size: 1.7em;
  color: #282c34;

  margin: 1em 100px;
}

.product-card {
  background: #fff;

  border-radius: 8px;

  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);

  /* Add more space at the bottom */
  padding-bottom: 1em;
}

.product-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.product-card img {
  width: 100%;
}

.product-name {
  text-align: left;
  font-size: 1.1em;
  font-weight: bold;

  padding-left: 1em; /* Move to the right */

  margin-top: 1em; /* Space from image */
}

.product-price {
  color: #5097ec;
  font-weight: bold;
  font-size: 1.2em;

  padding-left: 1em; /* Move to the right */
}

.product-detail {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.15);
  margin: 2em auto;
  max-width: 700px;
  padding: 2em 2em 1.5em 2em;
}

.back-link {
  display: inline-block;
  margin-bottom: 1.5em;
  color: #5097ec;
  text-decoration: none;
  font-weight: 500;
}

.back-link:hover {
  text-decoration: underline;
}

.product-detail-content {
  display: flex;
  gap: 2em;
  align-items: flex-start;
}

.product-detail-image {
  width: 240px;
  max-width: 100%;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.product-detail-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.product-detail-info h2 {
  margin: 0 0 0.5em 0;
  font-size: 2em;
  color: #282c34;
}

.product-detail-price {
  color: #5097ec;
  font-weight: bold;
  font-size: 1.3em;
  margin-bottom: 1em;
}

.product-detail-info p {
  color: #444;
  margin: 0;
}
